import React from "react";
import axios from "axios";
import CamaroteCarousel from "../camarotes/CamaroteCarousel.jsx";

class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = { blocos: [], camarotes: [], navios: [], hoteis: [] };
  }

  componentDidMount() {
    axios
      .get("./data/db.json")
      .then(
        function(response) {
          this.setState({
            blocos: response.data.produtos.blocos,
            camarotes: response.data.produtos.camarotes
          });
          console.log(response.data);
        }.bind(this)
      )
      .catch(function(error) {
        // handle error
        console.log(error);
      });
  }

  render() {
    //var camarotes = this.state.camarotes;
    //var listItems = camarotes.map(camarotes => (
    //  <li key={camarotes.toString()}>{camarotes}</li>
    //));

    const secaoTituloSt = { color: "#4A4A4A", fontSize: "2rem" };
    return (
      <div className="mb-5">
        <h1 className="text-white mb-4">HOME</h1>

        <div className="secao w-100">
          <h1 style={secaoTituloSt}>CAMAROTES</h1>
          <CamaroteCarousel />
        </div>

        <div className="secao w-100 mt-3">
          <h1 style={secaoTituloSt}>BLOCOS</h1>
          <CamaroteCarousel />
        </div>

        <h1 className="text-white mb-4">HOME</h1>
      </div>
    );
  }
}

export default Home;
